blockquote,
body,
button,
dd,
dl,
dt,
fieldset,
form,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
input,
legend,
li,
ol,
p,
pre,
td,
textarea,
th,
ul,
header,
main {
  margin: 0;
  padding: 0
}

body,
button,
input,
select,
textarea {
  font: 12px/1.5 tahoma, arial, 'Hiragino Sans GB', '\5b8b\4f53', sans-serif
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%
}

address,
cite,
dfn,
em,
var {
  font-style: normal
}

code,
kbd,
pre,
samp {
  font-family: courier new, courier, monospace
}

small {
  font-size: 12px
}

ol,
ul {
  list-style: none
}

a {
  text-decoration: none
}

a:hover {
  text-decoration: underline
}

sup {
  vertical-align: text-top
}

sub {
  vertical-align: text-bottom
}

legend {
  color: #000
}

fieldset,
img {
  border: 0
}

button,
input,
select,
textarea {
  font-size: 100%
}

button {
  border-radius: 0
}

table {
  border-collapse: collapse;
  border-spacing: 0
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'font_family';
  /* project id 1580587 */
  src: url('http://at.alicdn.com/t/font_1580587_r5b061mqbrs.eot');
  src: url('http://at.alicdn.com/t/font_1580587_r5b061mqbrs.eot?#iefix') format('embedded-opentype'),
    url('http://at.alicdn.com/t/font_1580587_r5b061mqbrs.woff2') format('woff2'),
    url('http://at.alicdn.com/t/font_1580587_r5b061mqbrs.woff') format('woff'),
    url('http://at.alicdn.com/t/font_1580587_r5b061mqbrs.ttf') format('truetype'),
    url('http://at.alicdn.com/t/font_1580587_r5b061mqbrs.svg#font_family') format('svg');
}

.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}

body {

  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}